<template>
  <div>
    <el-form :inline="true" class="demo-form-inline">
      <router-link to="/" active-class="router-active">
        <el-link></el-link>
      </router-link>
      <el-button
        class="el-icon-refresh"
        type="text"
        @click="refreshData">
      </el-button>
    </el-form>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item>
        <el-input
          v-model="search"
          class="search_id"
          size="mini"
          placeholder="ID">
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          type="text"
          @click="onSearch()"
          class="el-icon-search">
        </el-button>
      </el-form-item>
      <el-form-item>
        <el-button
          type="text"
          @click="getPages()"
          class="el-icon-search">
        </el-button>
      </el-form-item>
      <el-form-item>
        <el-button
          class="el-icon-circle-plus-outline"
          type="text"
          @click="dialogVisible = true">
        </el-button>
      </el-form-item>
    </el-form>
    <br>
    <el-switch v-model="showIdSwitch" active-value="true" inactive-value="false"
               active-text="ID" inactive-text="ID"
               style="height:50px;float: left" @change="changeShowId">
    </el-switch>
    <el-table
      :data="tableData"
      highlight-current-row
      border
      style="width: 100%">
      <el-table-column
        label=""
        v-if="showId">
        <template slot-scope="scope">
          <span>{{ scope.row.id }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="">
        <template slot-scope="scope">
          <span>{{ scope.row.content }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="">
        <template slot-scope="scope">
          <span>{{ scope.row.description }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="">
        <template slot-scope="scope">
          <span>{{ scope.row.status }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="">
        <template slot-scope="scope">
          <span>{{ scope.row.score }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="right">
            <p>ID: {{ scope.row.cycleType.id }}</p>
            <p>: {{ scope.row.cycleType.cronExpression }}</p>
            <p>{{ scope.row.cycleType.workdayStatus | changeWorkdayStatus }}</p>
            <p>{{ scope.row.cycleType.times }}</p>
            <div slot="reference" class="name-wrapper">
              <el-button type="text">{{ scope.row.cycleType.description }}</el-button>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column
        label="">
        <template slot-scope="scope">
          <i class="el-icon-time hidden-sm-and-down"/>
          <span>{{ scope.row.updateTime | formatDate1 }}</span>
        </template>
      </el-table-column>
      <!--      <el-table-column-->
      <!--        label="">-->
      <!--        <template slot-scope="scope">-->
      <!--          <el-popover trigger="hover" placement="right">-->
      <!--            <p>: {{ scope.row.userName }}</p>-->
      <!--            <p>: {{ scope.row.updateTime }}</p>-->
      <!--            <p>{{ scope.row.createTime }}</p>-->
      <!--            <div slot="reference" class="name-wrapper">-->
      <!--              <el-button type="text">{{ scope.row.id }}</el-button>-->
      <!--            </div>-->
      <!--          </el-popover>-->
      <!--        </template>-->
      <!--      </el-table-column>-->
      <el-table-column
        label=""
        fixed="right"
        width="200">
        <template slot-scope="scope">
          <el-button
            size="mini"
            icon="el-icon-edit"
            @click="handleEdit(scope.$index, scope.row)">
          </el-button>
          <el-button
            size="mini"
            type="danger"
            icon="el-icon-delete"
            @click="handleDelete(scope.$index, scope.row)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-form :model="taskForm" :rules="rules" ref="taskForm" label-width="70px" class="demo-taskForm" size="medium">
      <el-dialog
        title=""
        :append-to-body='true'
        :visible.sync="dialogVisible"
        width="80%"
        :before-close="handleClose">
        <el-input type="hidden" v-model="taskForm.id"/>
        <el-form-item label="" prop="content">
          <el-input v-model="taskForm.content"/>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input v-model="taskForm.description"/>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-input v-model="taskForm.status"/>
        </el-form-item>
        <el-form-item label="" prop="score">
          <el-input v-model="taskForm.score"/>
        </el-form-item>
        <el-form-item label="" prop="cycleTypeId">
          <el-select v-model="taskForm.cycleTypeId">
            <el-option
              v-for="(cycleType, index) in cycleTypeList"
              :key="index"
              :value="cycleType.id"
              :label="cycleType.description"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="startTime">
          <el-date-picker type="datetime" placeholder="" v-model="taskForm.startTime"
                          style="width: 100%;"/>
        </el-form-item>
        <el-form-item label="" prop="endTime">
          <el-date-picker type="datetime" placeholder="" v-model="taskForm.endTime"
                          style="width: 100%;"/>
        </el-form-item>

        <span slot="footer" class="dialog-footer">
            <el-button @click="cancel()" size="medium"> </el-button>
            <el-button @click="addTask()" type="primary" size="medium"> </el-button>
          </span>
      </el-dialog>
    </el-form>

    <el-form :model="taskForm" :rules="rules" ref="taskForm" label-width="70px" class="demo-taskForm" size="medium">
      <el-dialog
        title=""
        :append-to-body='true'
        :visible.sync="dialogUpdate"
        width="60%"
        :before-close="handleClose">
        <el-input type="hidden" v-model="taskForm.id"/>
        <el-form-item label="" prop="content">
          <el-input v-model="taskForm.content"/>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input v-model="taskForm.description"/>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-input v-model="taskForm.status"/>
        </el-form-item>
        <el-form-item label="" prop="score">
          <el-input v-model="taskForm.score"/>
        </el-form-item>
        <el-form-item label="" prop="cycleTypeId">
          <el-select v-model="taskForm.cycleTypeId">
            <el-option
              v-for="(cycleType, index) in cycleTypeList"
              :key="index"
              :value="cycleType.id"
              :label="cycleType.description"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="userDate">
          <el-date-picker type="datetime" placeholder="" v-model="taskForm.startTime"
                          style="width: 100%;"/>
        </el-form-item>
        <el-form-item label="" prop="userDate">
          <el-date-picker type="datetime" placeholder="" v-model="taskForm.endTime"
                          style="width: 100%;"/>
        </el-form-item>

        <span slot="footer" class="dialog-footer">
            <el-button @click="cancel()" size="medium"> </el-button>
            <el-button @click="updateTask()" type="primary" size="medium"> </el-button>
          </span>
      </el-dialog>
    </el-form>
    <br>
    <div class="pages">
      <el-pagination
        background
        :disabled="disablePage"
        :current-page.sync="currentPage"
        small
        layout="prev, pager, next"
        :page-size="pageSize"
        :total="total"
        @current-change="handleCurrentChange">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  // import {formatDate} from "../utils/formatDate.js";//
  export default {
    data() {
      return {
        taskForm: {
          id: '',
          content: '',
          description: '',
          cycleTypeId: '',
          status: '',
          score: ''
        },
        taskFormView: {
          id: '',
          content: '',
          description: '',
          cycleType: {
            cronExpression: '',
            times: 0,
            createTime: '',
            workdayStatus: 0,
            description: '',
            updateTime: '',
            id: 1
          },
          status: '',
          score: '',
          updateTime: '',
          createTime: ''
        },
        rules: {
          content: [
            {required: true, message: '', trigger: 'blur'},
            {min: 1, max: 20, message: ' 1  20 ', trigger: 'blur'}
          ],
          cycleTypeId: [
            {required: true, message: '', trigger: 'blur'}
          ],
          score: [
            {required: true, message: '', trigger: 'blur'}
          ],
        },
        cycleTypeList: [
          {
            id: 2,
            cronExpression: "0 0 0 * * ? *",
            times: 0,
            workdayStatus: 0,
            description: "",
          },
        ],
        tableData: [],
        search: '',
        dialogVisible: false,
        dialogUpdate: false,
        pageSize: 10,
        currentPage: 1,
        total: 0,
        disablePage: false,
        showIdSwitch: false,
        showId: false,
      }
    },
    methods: {
      changeShowId(data) {
        this.showId = data === "true";
      },
      handleEdit(index, row) {
        this.dialogUpdate = true;
        this.taskForm = Object.assign({}, row); //
      },
      handleDelete(index, row) {
        console.log(index, row);
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.axios({
            method: 'delete',
            url: '/v1/task/' + row.id
          }).then(response => {
            this.axios.get('/v1/task/').then(response => {
              this.tableData = response.data;
            }).catch(error => {
              console.log(error);
            });
            this.$message({
              type: 'success',
              message: '!'
            });
            console.log(response);
            location.reload();
          }).catch(error => {
            console.log(error);
          });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      handleClose(done) {
        this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {
          });
      },
      handleCurrentChange() {
        console.log(`: ${this.currentPage}`);
        let postData = this.qs.stringify({
          page: this.currentPage
        });
        this.axios({
          method: 'get',
          url: '/v1/task',
          data: postData
        }).then(response => {
          this.tableData = response.data;
        }).catch(error => {
          console.log(error);
        });
      },
      cancel() {
        this.dialogUpdate = false;
        this.dialogVisible = false;
        this.emptyTaskData();
      },
      emptyTaskData() {
        this.taskForm = {
          id: '',
          content: '',
          description: '',
          cycleTypeId: '',
          status: '',
          score: ''
        }
      },
      addTask() {
        let postData = JSON.stringify({
          content: this.taskForm.content,
          description: this.taskForm.description,
          cycleTypeId: this.taskForm.cycleTypeId,
          status: this.taskForm.status,
          score: this.taskForm.score
        });
        this.axios({
          method: 'post',
          url: '/v1/task',
          data: postData
        }).then(response => {
          this.axios.get('/v1/task').then(response => {
            this.tableData = response.data;
            this.currentPage = 1;
            this.$message({
              type: 'success',
              message: '!'
            });
          }).catch(error => {
            console.log(error);
          });
          this.getPages();
          this.dialogVisible = false;
          console.log(response);
        }).catch(error => {
          console.log(error);
        });
      },
      updateTask() {
        let postData = this.qs.stringify({
          id: this.taskForm.id,
          content: this.taskForm.content,
          description: this.taskForm.description,
          cycleTypeId: this.taskForm.cycleTypeId,
          status: this.taskForm.status,
          score: this.taskForm.score
        });
        this.axios({
          method: 'put',
          url: '/v1/task',
          data: postData
        }).then(response => {
          this.handleCurrentChange();
          this.cancel();
          this.$message({
            type: 'success',
            message: '!'
          });
          console.log(response);
        }).catch(error => {
          this.$message({
            type: 'success',
            message: '!'
          });
          console.log(error);
        });
      },
      onSearch() {
        let postData = this.qs.stringify({
          userName: this.search
        });
        this.axios({
          method: 'post',
          url: '/ListByName',
          data: postData
        }).then(response => {
          this.tableData = response.data;
          this.disablePage = true;
        }).catch(error => {
          console.log(error);
        });
      },
      getPages() {
        this.axios.get('/v1/task').then(response => {
          this.tableData = response.data;
        }).catch(error => {
          console.log(error);
        });
      },
      refreshData() {
        location.reload();
      },
      // created() {
      //   /*this.axios.get('static/user.json').then(response =>
      //   {
      //       this.tableData = response.data.tableData;
      //       this.total = response.data.tableData.length;
      //       // console.log(JSON.parse(JSON.stringify(response.data))['tableData'])
      //   });*/
      //   this.axios.post('/v1/task').then(response => {
      //     this.tableData = response.data;
      //   }).catch(error => {
      //     console.log(error);
      //   });
      //
      //   // this.axios.post('/rows').then(response => {
      //   //   this.total = response.data;
      //   // }).catch(error => {
      //   //   console.log(error);
      //   // });
      // },
    },

    filters: {
      formatDate1: function (value) {// 
        if (value == null) {
          return '';
        } else {
          let date = new Date(value);
          let y = date.getFullYear();// 
          let MM = date.getMonth() + 1;// 
          MM = MM < 10 ? ('0' + MM) : MM;
          let d = date.getDate();// 
          d = d < 10 ? ('0' + d) : d;
          let h = date.getHours();// 
          h = h < 10 ? ('0' + h) : h;
          let m = date.getMinutes();// 
          m = m < 10 ? ('0' + m) : m;
          let s = date.getSeconds();// 
          s = s < 10 ? ('0' + s) : s;
          return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
        }
      },
      changeWorkdayStatus(value) {
        if (value === '') {
          return ''
        } else if (value === 0) {
          return ''
        } else if (value === 1) {
          return ''
        } else if (value === 2) {
          return ''
        }
      },
    },

    created() {
      /*this.axios.get('static/user.json').then(response =>
           {
               this.tableData = response.data.tableData;
               this.total = response.data.tableData.length;
               // console.log(JSON.parse(JSON.stringify(response.data))['tableData'])
           });*/
      this.axios.get('/v1/task').then(response => {
        this.tableData = response.data;
      }).catch(error => {
        console.log(error);
      });

      this.axios.get('/v1/cycleType').then(response => {
        this.cycleTypeList = response.data;
      }).catch(error => {
        console.log(error);
      });

      // this.axios.get('/v1/task/'+row.id).then(response =>
      // {
      //   this.total = response.data;
      // }).catch(error =>
      // {
      //   console.log(error);
      // });
    }
  }
</script>
<style scoped>
  .search_name {
    width: 200px;
  }

  .pages {
    margin: 0px;
    padding: 0px;
    text-align: right;
  }
</style>



